@media screen and (min-width: 1024px) {
  .root--container {
    padding: 4rem;
    margin: 100px auto 6rem;
  }
  .root--container-grid {
    display: grid;
    grid-template-columns: 8fr 4fr;
    grid-column-gap: 3.5rem;
    column-gap: 3.5;
  }
  .col2-2-grid {
    display: grid;
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
    border: 1px solid #e6e6e6;
    border-radius: 0.5rem;
    padding: 1rem;
    margin-top: 0;
  }
}
.video-icon {
  margin: 0 0 0 -75px;
  font-size: 36px;
  color: #292929;
}
.root--container {
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-wrap: break-word;
  word-break: break-word;
  padding: 4rem;
  margin: 100px auto 6rem;
}
.root--container-grid {
  display: grid;
  grid-template-columns: 8fr 4fr;
  grid-column-gap: 3.5rem;
  column-gap: 3.5;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  margin: 0 auto;
}
.root--container-grid a {
  text-decoration: none;
  color: inherit;
}
.video-news-card {
  position: relative;
}
.card-image-figure {
  background-color: #f7f7f7;
  position: relative;
}
figure img {
  image-rendering: auto !important;
  max-width: 100%;
  margin: 0;
  padding: 0;
}
@media screen and (min-width: 768px) {
  .card-image-headline {
    position: absolute;
    opacity: 0.85;
    background-color: var(--bg3-color);
    padding: var(--space1);
    bottom: 35px;
    width: 78%;
    border-radius: 0 5px 5px 0;
  }
  .headline-type-5 {
    color: #292929;
    font-weight: 700;
    line-height: 1.38;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-size: 16px;
  }
}
.card-image-headline {
  position: absolute;
  opacity: 0.85;
  background-color: #fff;
  padding: 1rem;
  width: 78%;
  border-radius: 0 5px 5px 0;
}
